<h3 id="teaching" tabindex="-1"><a class="header-anchor" href="#teaching" aria-hidden="true">#</a> Teaching</h3>
<ul>
<li>A part module of the <a href="https://www.neuphil.uni-wuerzburg.de/anglistik/studium/im-studium/zusatzzertifikat-digitale-kompetenz/" target="_blank" rel="noopener noreferrer">Zusatzzertifikat
Digital Competence<OutboundLink/></a>
, which offers modern languages students
the chance to acquire and prove competencies in the handling of
digital data (University of Würzburg, since winter semester 2020).</li>
<li>Text digitization with OCR4all as part of the &quot;Basismodul
Digitalisierung&quot; of the chair of Computer Philology and Modern German
Literary Studies (University of Würzburg, since summer semester 2020).</li>
<li>Digitization of historical printed works with OCR4all as part of
the “Aufbaumodul Analysepraxis der Sprachwissenschaft” of the chair of
German Linguistics (University of Würzburg, summer semester 2020).</li>
<li>OCR internship for Master's students of the study program <a href="http://www.mfn.uni-wuerzburg.de/masterstudiengang/" target="_blank" rel="noopener noreferrer">Mittelalter
und Frühe Neuzeit<OutboundLink/></a>
(University of Würzburg, since winter semester 2018/19).</li>
<li>Seminar Historische Korpuslinguistik held at the Humboldt
University Berlin: in cooperation with the ZPD several works on the
subject of herbs from the 17th and 18th century were transcribed by
Master’s students. The required calculations were processed on the
servers in Würzburg, while the students were able to make the
necessary corrections remotely and conveniently via a web interface
after a short briefing.</li>
</ul>
