<h3 id="application-and-diffusion-assortment" tabindex="-1"><a class="header-anchor" href="#application-and-diffusion-assortment" aria-hidden="true">#</a> Application and diffusion (assortment)</h3>
<ul>
<li>Wide range of usage within the university of Würzburg, e.g.:
<ul>
<li>extensive use in the field of early modern prints conducted by the
College of Middle Ages and Early Modern Period</li>
<li>bulk digital recording of fracture novels of the 19th century at
the chair of Computer Philology (over 1,000 novels have already been
processed)</li>
<li>Participation in several project proposals, including a project in
cooperation with the HU Berlin for the indexing of the Digital Fairy
Tale Reference Library of Jacob and Wilhelm Grimm, as well as the
proposed academy project Sprachgitter digital: Historical-Critical
Jean Paul Edition</li>
</ul>
</li>
<li>Recently approved project <a href="https://kulturarvscluster.kb.dk/projekter/danish-neo-latin-literature-digitalisering-af-danmarks-latinsksprogede-kulturarv-fra-perioden-1482-1600" target="_blank" rel="noopener noreferrer">Danish
Neo-Latin Literature<OutboundLink/></a>
(Aarhus University, National Cultural
Heritage Cluster, Danish Royal Library): High-quality (comprehensive
GT creation and work-specific training) registration of 856 Danish
prints from 1482 to 1600</li>
<li><a href="https://www.mimotext.uni-trier.de/" target="_blank" rel="noopener noreferrer">MiMoText<OutboundLink/></a> project
at the Kompetenzzentrum of the University of Trier: Recording French
novels of the 18th century</li>
<li>Monumenta Germaniae Historica: High-quality recording of
encyclopedias from the incunabula period (cooperation with the ZPD for
the preparation of a LIS project application)</li>
<li>Department of English, University of Bristol: The Literary Heritage
of Anglo-Dutch Relations, 1050-1600</li>
<li>Universidad Nacional de Educación a Distancia (Madrid): Project for
the recording of Latin texts of the 15th and 16th centuries</li>
<li>Kommission für bayerische Landesgeschichte an der Bayerischen
Akademie der Wissenschaften: recording of various printed works and
typewritten products</li>
<li>WiTTFind project at the CIS of the LMU München: Processing of
different material, including strengthened typewriter pages</li>
</ul>
